import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import styled, {css} from 'styled-components';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';

import { LayoutGrid } from '../layout/layout';
import BaseLink from '../base/links/base-link';
import Card from '../base/card';

import breakpoints from '../layout/breakpoints';

const WorkGridWrapper = ({posts}) => {
    return (
        <LayoutGrid columnCenter maxWidth='70rem' >
            {posts.map(item => <WorkCard key={item.id} title={item.name} slug={item.slug} featuredImage={item.featuredImage.localFile} />)}
        </LayoutGrid>
    )
};

const WorkCard = ({title, slug, featuredImage}) => {
    const image = getImage(featuredImage);

    return (
    <BaseLink to={`/work/${slug}`}>
        <WorkCardWrapper>
                <WorkCardImage image={image} alt="screenshot" />  
                <WorkCardText>{title}</WorkCardText> 
        </WorkCardWrapper>
    </BaseLink>
    )
};

const WorkCardWrapper = styled(Card)`
    z-index: 0;
    overflow: hidden;



    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: var(--color-primary-1);
        opacity: 0;
        transition: all var(--transition-base);
        z-index: 1;
    }

    &:hover::after {
        opacity: 0.95;   
    }
`;

const WorkCardImage = styled(GatsbyImage)`
    height: 100%;
`;

const WorkCardText = styled.span`
    position: absolute;
    left: var(--spacing-m);
    bottom: -5rem;
    margin: 0;
    color: var(--color-neutral-0);
    font-size: var(--font-size-l);
    font-weight: var(--font-weight-bold);
    transition: all var(--transition-fast);
    z-index: 2;

    ${WorkCardWrapper}:hover & {
        bottom: var(--spacing-m);
    }
`;

const WorkFilters = ({selectedCategory, setCategory}) => {
    const data = useStaticQuery(graphql`
        query getCategories {
            allStrapiCategory {
                nodes {
                    name
                }
            }
        }       
    `);

    const handleChange = (e) => {
        setCategory(e.target.dataset.category);
    }

    return (
        <WorkFiltersWrapper>
            <WorkFilterButton key="All" onClick={handleChange} data-category="" $isActive={selectedCategory === ""}>All</WorkFilterButton>
            {data.allStrapiCategory.nodes.slice(0).reverse().map(item => <WorkFilterButton key={item.name} $isActive={selectedCategory === item.name} onClick={handleChange} data-category={item.name}>{item.name}</WorkFilterButton>)}
        </WorkFiltersWrapper>
    )
};

const WorkFiltersWrapper = styled.div`
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    margin-top: var(--spacing-m);   
`;

const WorkFilterButton = styled.button`
    margin: 0;
    padding: var(--spacing-base); 
    min-width: 4rem;
    font-weight: var(--font-weight-semibold);
    color: ${({ $isActive }) => $isActive ? 'var(--color-neutral-1)' : 'var(--color-text)'};
    background-color: ${({ $isActive }) => $isActive ? 'var(--color-neutral-8)' : 'var(--color-neutral-4)'};
    border: none;
    border-radius: 0;
    cursor: pointer;
    appearance: none;

   ${({ $isActive }) => $isActive && css`
        .dark-mode & {
            color: var(--color-text);
        }
    `}

    ${breakpoints.sm`
        min-width: 6rem;
        padding: var(--spacing-base) var(--spacing-m);
    `}
`;

export {WorkGridWrapper, WorkFilters}