import React from 'react';
import { graphql } from 'gatsby';

import Page from '../components/layout/page';
import PageHead from '../components/base/head-base';
import { LayoutContainer, LayoutItemWrapper, LayoutRow } from '../components/layout/layout';
import { Heading } from '../components/base/typography';
import { WorkFilters, WorkGridWrapper } from '../components/page-content/work-content';

const WorkPage = ({ data }) => {
  const { allStrapiWork } = data;
  const posts = [...allStrapiWork.nodes];
  const [selectedCategory, setSelectedCategory] = React.useState("")


  const handleSetCategory = (category) => {
    setSelectedCategory(category);
  }

  return (
    <>
      <Page active="work">
        <LayoutContainer>
          <LayoutRow>
            <LayoutItemWrapper rowCenter>
              <Heading level={1}>Work</Heading>
              <WorkFilters
                selectedCategory={selectedCategory}
                setCategory={handleSetCategory} />
              <WorkGridWrapper
                posts={posts.filter(item => selectedCategory === "" || item.category.name === selectedCategory)}
              />
            </LayoutItemWrapper>
          </LayoutRow>
        </LayoutContainer>
      </Page>
    </>
  )
}

export const pageQuery = graphql`
  query getWork {
    allStrapiWork(sort: {createdAt: DESC}) {
      nodes {
        id
        name
        slug
        description {
          data {
            description
          }
        }
        demoUrl
        category {
          name
        }
        workphotos {
          name
          localFile {
            childImageSharp {
              gatsbyImageData(width: 600, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
            }
          }
        }
        featuredImage {
          localFile {
            childImageSharp {
              gatsbyImageData(width: 200, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
            }
          }
        }
      }
    }
  }
`


export const Head = ({ location }) => {
  return (
    <PageHead
      pathname={location.pathname}
      title="Work - Jan Wagenaar"
    />
  )
};

export default WorkPage;